<style>
.pregfreq{
  color: #4C24FD;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  
 
}
.v-card--reveal {
  align-items: center;
  bottom: 0;
  justify-content: center;
  opacity: .5;
  position: absolute;
  width: 100%;
}
.sedes{
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.celular{
  height: 150px;
}
.bouncy-castle {  
  width:600px;
  height:600px;
  margin:0 auto;
  position:relative;
  }
  .bouncy-castle2 {  
  width:650px;
  height:650px;
  }
  .bouncy-castle3 {  
  width:150px;
  height:150px;
  }
  .ball { 
    position: absolute;
    border-radius: 50%;
    height: 270px;
    width: 270px;
    top:0;
    left:50%;
    transform:translate(-50%,0);
    -webkit-animation-direction: alternate;
    -webkit-animation-duration: 1s;
    -webkit-animation-name: my-bounce;
    -webkit-animation-iteration-count: infinite;
    animation-direction: alternate;
    animation-duration: 1s;
    animation-name: my-bounce;
    animation-iteration-count: infinite;
   
  }
@-webkit-keyframes my-grow {
 from {   
    width: 200px;
    height:50px;
    top: 230px;
  }

  to {
    width: 150px;
    height:10px;
    top: 240px;
    
  }
}

@keyframes my-grow {
  from {   
    width: 200px;
    height:50px;
    top: 230px;
  }

  to {
    width: 150px;
    height:10px;
    top: 240px;
  }
}

@-webkit-keyframes my-bounce {
  from {
    top: 0;
  }

  to {
    top: 10%;
  }
}

@keyframes my-bounce {
  from {
    top: 0;
  }

  to {
    top: 10%;
  }
}
</style>
<script>
const axios = require('axios');
  export default {
    data () {
      return {
        preguntasFrecuentes:{
          medicina:[],
          derecho:[],
          psicologia:[]
      },
        dialogMedicina: false,
        dialogDerecho: false,
        dialogPsicologia: false,
         links: [
        'fab fa-facebook',
        'fab fa-twitter',
        'fab fa-youtube',
        'fab fa-linkedin',
        'fab fa-instagram',
      ],
      areas:[
        {name:'MEDICINA',
        src:'https://1.bp.blogspot.com/-6rBYlBRbYFg/XQEPWkqhz6I/AAAAAAAAACQ/662tmCEnZk0SUKmyfeWYvLFtTKD1-ZMxACLcBGAs/s320/pedro%2Bxd.jpg'
        },{name:'PSICOLOGIA',
        src:'https://waarket.com/wp-content/uploads/2018/04/waarket-psicologia-en-diseno.jpg'
        },{name:'DERECHO',
        src:'https://aquisehabladerecho.files.wordpress.com/2017/08/doc-05.jpg'
        }
      ],
        items: [
          {
            name:'COCHABAMBA',src: 'https://www.lostiempos.com/sites/default/files/styles/noticia_detalle/public/media_imagen/2018/7/14/14_me_2_unifranz.jpg?itok=rynuK-ku',
          },
          {
            name:'EL ALTO',src: 'https://lh3.googleusercontent.com/p/AF1QipN4N2uSmtuid-A22l2W1WfayOnwPvzxDGKIMhD0=s1600-w400',
          },
          {
            name:'LA PAZ',src: 'https://unifranz.edu.bo/wp-content/uploads/2018/01/Fotografias-Universidad-1.jpg',
          },
          {
            name:'SANTA CRUZ',src: 'https://www.money.com.bo/images/2017/07/Foto_Sede_UNIFRANZ_Santa_Cruz.jpg',
          },
        ],
        
       
      }
    },
    mounted(){},
    methods:{
      preguntasAreas(data){
        console.log(data)
        if (data==0) {
          console.log("medicina")
          this.dialogMedicina=true
          axios.get('https://covidhelper-backend.herokuapp.com/frequentQuestions/medicina')
          .then((res) => {
            console.log(res.data)
            this.preguntasFrecuentes.medicina=res.data
          })
        }
         if (data==1) {
          console.log("psicologia")
          this.dialogPsicologia=true
           axios.get('https://covidhelper-backend.herokuapp.com/frequentQuestions/psicologia')
           .then((res) => {
             console.log(res.data)
             this.preguntasFrecuentes.psicologia=res.data
           })
        }
         if (data==2) {
          console.log("derecho")
          this.dialogDerecho=true
          axios.get('https://covidhelper-backend.herokuapp.com/frequentQuestions/derecho')
          .then((res) => {
            console.log(res.data)
            this.preguntasFrecuentes.derecho=res.data
          })
        }

      }
    }
  }
</script>