  asignacionPreguntasFormulario(){
    var token= jwt_decode(localStorage.getItem('user_token'));
    var status=0;
    if (this.form.frm_status==false) {
      status=0
    }else{
      status=1
    }
    axios.post('https://covidhelper-backend.herokuapp.com/create_form',{
        "id_area":localStorage.getItem('id_area'),
        "id_user":token.id,
        "frm_name":this.form.frm_name,
