        "frm_status":status
    })
    .then((result) => {
      console.log(result.data)
      console.log(this.listQuestionsOriginal)
      for (let i = 0; i < this.listQuestionsOriginal.length; i++) {
        axios.post('https://covidhelper-backend.herokuapp.com/create_forms_question',{
          "id_form":result.data.Form.id,
          "id_question":this.listQuestionsOriginal[i].id      
        })
        .then((Form) => {
          console.log(Form.data)
                this.dialogTablaPreguntas=false
                this.newQuestion=false
                this.dialog=false
        })
      }

    })

  },
  subirPosicion(data){
    if (data>0) {
      this.arrOld=this.listQuestionsOriginal[data-1]
      this.listQuestionsOriginal[data-1]=this.listQuestionsOriginal[data]
      this.listQuestionsOriginal[data]=this.arrOld
      this.arrOld=[]
      this.arrNew=this.listQuestionsOriginal
      this.listQuestionsOriginal=[]
      this.listQuestionsOriginal=this.arrNew
    }
  },
  cancelDialogTablaPreguntas(){
    this.dialogTablaPreguntas=false
    this.listQuestionsOriginal=[];
  },
  tableQuestions(){
    this.dialogTablaPreguntas=true
    for (let i = 0; i < this.listQuestions.length; i++) {
      if (this.listQuestions[i].estado==true) {
        this.listQuestionsOriginal.push(this.listQuestions[i])
      }
    }
  },
  saveQuestion(){
    axios.post('https://covidhelper-backend.herokuapp.com/create_question',
    {
        "id_typ_ans":this.question.id_typ_ans,
        "id_area":localStorage.getItem('id_area'),
        "qst_question":this.question.qst_question
    })
    .then((result) => {
      this.contador=this.listQuestions.length
      this.listQuestions.push(result.data.result)
      this.listQuestions[this.contador].estado=true
      this.dialogNewQuestion=false
      this.contador=0
    })
  },
  addQuestion(){
    this.dialogNewQuestion=true
