<template>
  <v-data-table
    :headers="headers"
    :items="listFrequentQuestions"
    :search="search"
    sort-by="calories"
    class="elevation-1"
  >
    <template v-slot:top>
      <v-toolbar flat color="white">
        <v-toolbar-title>LISTA DE FORMULARIOS</v-toolbar-title>
        <v-divider
          class="mx-4"
          inset
          vertical
        ></v-divider>
        <v-text-field
        v-model="search"
        append-icon="mdi-magnify"
        label="Buscar"
        single-line
        hide-details
      ></v-text-field>
        <v-spacer></v-spacer>
        <v-dialog v-model="dialog" max-width="1000px">
          <template v-slot:activator="{ on }">
            <v-btn color="primary" dark class="mb-2" v-on="on" @click="prueba">CREAR NUEVA PREGUNTA FRECUENTE</v-btn>
          </template>
          <v-card>
             <form ref="form" @submit.prevent="saveNewFrequentQuestion" :v-model="valid" lazy-validation>
            <v-card-title>
              <span class="headline">NUEVA PREGUNTA FRECUENTE</span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12" sm="6" md="6">
                    <v-textarea :rules="rules" clearable clear-icon="cancel" auto-grow rows="3" row-height="3" label="Pregunta" required  v-model='questionFrequent.frq_qst_question'></v-textarea>
                  </v-col>
                  <v-col cols="12" sm="6" md="6">
                    <v-textarea :rules="rules" clearable clear-icon="cancel" auto-grow rows="3" row-height="3" label="Respuesta" required v-model='questionFrequent.frq_qst_answer'></v-textarea>
                  </v-col>
                  <v-col cols="12" sm="6" md="6">
                    <v-select  :items="listAreas" label="rea" required item-value="id" item-text="ars_name" v-model='questionFrequent.id_area'></v-select>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialog=false,reloadPage()">CANCELAR</v-btn>
              <v-btn color="blue darken-1" text type="submit" @click="reloadPage()">GUARDAR</v-btn>
            </v-card-actions>
            </form> 
          </v-card>
        </v-dialog>
          <v-dialog v-model="dialogEdit" max-width="1000px">
          <v-card>
            <v-card-title>
              <span class="headline">EDICIN DE FORMULARIO</span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12" sm="6" md="6">
                    <v-textarea :rules="rules" clearable clear-icon="cancel" auto-grow outlined rows="1" row-height="4" label="Pregunta" required :value=questionFrequent.frq_qst_question v-model='questionFrequent.frq_qst_question'></v-textarea>
                  </v-col>
                  <v-col cols="12" sm="6" md="6">
                    <v-textarea :rules="rules" clearable clear-icon="cancel" auto-grow outlined rows="1" row-height="4" label="Respuesta" required :value=questionFrequent.frq_qst_answer v-model='questionFrequent.frq_qst_answer'></v-textarea>
                  </v-col>
                  <v-col cols="12" sm="6" md="6">
                    <v-select  :items="listAreas" label="Area" required item-value="id" item-text="ars_name" v-model='questionFrequent.id_area'></v-select>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialogEdit=false,reloadPage()">CANCELAR</v-btn>
              <v-btn color="blue darken-1" text @click="editQuestionFrequent(),reloadPage()">GUARDAR</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
    <template v-slot:item.action="{ item }">
      <v-icon
        small
        class="mr-2"
        @click="editItem(item)"
      >
        edit
      </v-icon>
      <v-icon
        small
        @click="deleteItem(item), reloadPage()"
      >
        delete
      </v-icon>
    </template>
    <template v-slot:item.actions="{ item }">
<div v-show="item.frq_state==1">
   <v-tooltip bottom>
      <template v-slot:activator="{ on }">
       <v-icon
        small
        class="mr-2"
        dark v-on="on"
        text icon color="blue lighten-2"
        @click="editStatusItem(item)"
      >
       mdi-thumb-up
      </v-icon>
      </template>
      <span>Deshabilitar</span>
    </v-tooltip>
</div>
<div v-show="item.frq_state==0">
         <v-tooltip bottom>
      <template v-slot:activator="{ on }">
       <v-icon
        small
        class="mr-2"
        dark v-on="on"
        text icon color="red lighten-2"
        @click="editStatusItem(item)"
      >
       mdi-thumb-down
      </v-icon>
      </template>
      <span>Habilitar</span>
    </v-tooltip>
</div>
    </template>
    <template v-slot:no-data>
      <v-btn color="primary" @click="initialize">Reset</v-btn>
    </template>
  </v-data-table>
</template>
<script>
import axios from 'axios';
import jwt_decode from 'jwt-decode';  
  export default {
    data: () => ({
        rules: [v => v.length <= 250 || 'Max 250 characters'],
      imageName:'',
      imageFile: '',
        imageUrl: '',
         search: '',
        model: null,
      dialogEdit:false,
      dialog: false,
      Estado:"",
      estado: [
           {id:'0',name:'Inactivo'},
            {id:'1',name:'Activo'}
           ],
      Rol:"",
      rol: [
           {id:'0',name:'Administrador General'},
            {id:'1',name:'Administrador Promedio'}
           ],
      headers: [
        {
          text: 'Nro',
          align: 'center',
          sortable: false,
          value: 'id',
        },
        { text: 'Pregunta', value: 'frq_qst_question' ,sortable: true,},
        { text: 'Respuesta', value: 'frq_qst_answer' },
        { text: 'rea', value: 'covid_area.ars_name' },
        { text: 'Creado por', value: 'covid_user.usr_name' },
        { text: 'Estado', value: 'actions' },
        { text: 'Acciones', value: 'action', sortable: false },
      ],
      desserts: [],
      listFrequentQuestions:[],
      listAreas:[],
      listHearquarters:[],
      editedIndex: -1,
      editedItem: {
        name: '',
        calories: 0,
        fat: 0,
        carbs: 0,
        protein: 0,
      },
      userID:'',
      questionFrequent:{
        id_area:'',
        id_user:'',
        frq_qst_question:'',
        frq_qst_answer:'',
        frq_state:''
      },
      defaultItem: {
        name: '',
        calories: 0,
        fat: 0,
        carbs: 0,
        protein: 0,
      },
    }),

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'Nuevo usuario' : 'Edit Item'
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
    },

    created () {
      this.initialize()
    },
    mounted(){
      console.log(localStorage.getItem('id_area'))
    axios.get('https://covidhelper-backend.herokuapp.com/frequentQuestion')
    .then((result) => {
      console.log('hola')
        console.log(result.data)
        console.log('hola')
        this.listFrequentQuestions=result.data
    }).catch((err) => {
        console.log(err)
    });
    axios.get('https://covidhelper-backend.herokuapp.com/areas')
    .then((result) => {
        console.log(result.data)
        this.listAreas=result.data
    }).catch((err) => {
        console.log(err)
    });
    },

    methods: {
            pickFile () {
        
        this.$refs.image.click ()
        console.log(this.imageName)
        },
        reloadPage(){
          window.location.reload()
        },
        onFilePicked (e) {

          const files = e.target.files
          // console.log(e)
          if(files[0] !== undefined) {
            this.imageName = files[0].name
            if(this.imageName.lastIndexOf('.') <= 0) {
              return
            }
            const fr = new FileReader ()
            fr.readAsDataURL(files[0])
            fr.addEventListener('load', () => {
              this.imageUrl = fr.result
              // console.log('hehe')
              console.log(files[0].name)
              // console.log('hehe')
              // this.newCourse.imageFile = files[0]
              axios.post('https://passunifranz-backapp.herokuapp.com/image',{"image":files[0]},{headers: {'Content-Type': 'multipart/form-data'}})
              .then((result) => {
               console.log(result) 
              }).catch((err) => {
                console.log(err)
              });
            })
          } else {
            this.imageName = ''
            this.imageFile = ''
            this.imageUrl = ''
          }
          },
        prueba(){
            console.log('prueba')
            console.log(this.search)
        },
      editItem (item) {
        console.log(item.id)
          this.userID=item.id
          this.questionFrequent.id_area=item.id_area
          this.questionFrequent.frq_qst_question=item.frq_qst_question
          this.questionFrequent.frq_qst_answer=item.frq_qst_answer
          this.dialogEdit = true
      },
      editStatusItem (item) {
        console.log(item.frq_state)
        
        if (item.frq_state==0) {
          axios.put('https://covidhelper-backend.herokuapp.com/frequentquestion/'+item.id,
        {
          "id_area":item.id_area,
          "frq_qst_question":item.frq_qst_question,
          "frq_qst_answer":item.frq_qst_answer,
          "frq_state": 1
        }
        )
        .then((result) => {
          console.log(result.data)
          this.reloadPage();
        })
        }else{
        axios.put('https://covidhelper-backend.herokuapp.com/frequentquestion/'+item.id,
        {
          "id_area":item.id_area,
          "frq_qst_question":item.frq_qst_question,
          "frq_qst_answer":item.frq_qst_answer,
          "frq_state": 0
        }
        )
        .then((result) => {
          console.log(result.data)
          this.reloadPage();
        })
        }
       
      },

      deleteItem (item) {
        console.log(item.id)
        axios.put('https://covidhelper-backend.herokuapp.com/frequentquestion/'+item.id,
        {
          "id_area":item.id_area,
          "frq_qst_question":item.frq_qst_question,
          "frq_qst_answer":item.frq_qst_answer,
          "frq_state": 0
        }
        )
        .then((result) => {
          console.log(result.data)
        })
      },
      editQuestionFrequent(){
        console.log(this.questionFrequent)
        axios.put('https://covidhelper-backend.herokuapp.com/frequentquestion/'+this.userID,
        {
          "id_area":this.questionFrequent.id_area,
          "frq_qst_question":this.questionFrequent.frq_qst_question,
          "frq_qst_answer":this.questionFrequent.frq_qst_answer,
          "frq_state":'1'
        }
        )
        .then((result) => {
          console.log(result.data)
          this.dialogEdit = false
        })
      },
      saveNewFrequentQuestion(){
        console.log(this.questionFrequent)
        var token= jwt_decode(localStorage.getItem('user_token'));
        console.log(token.id)
        axios.post( 'https://covidhelper-backend.herokuapp.com/create/frequentQuestion',
        {
          "id_area":this.questionFrequent.id_area,
          "id_user":token.id,
          "frq_qst_question":this.questionFrequent.frq_qst_question,
          "frq_qst_answer":this.questionFrequent.frq_qst_answer,
          "frq_state":'1'
        })
        .then((result) => {
          console.log(result.data)
          this.dialog = false
        })
      },
      close () {
        this.dialog = false
        setTimeout(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        }, 300)
      },

      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.desserts[this.editedIndex], this.editedItem)
        } else {
          this.desserts.push(this.editedItem)
        }
        this.close()
      },
    },
  }
</script>