<template >
<div>
        <v-navigation-drawer v-model="drawer" temporary app>
          <v-list nav dense v-if="estado == 'home'">
            <v-list-item
              v-for="item in menuItems"
              :key="item.title"
              active-class="deep-purple--text text--accent-4"
            >
              <v-list-item-content>
                <v-list-item-title @click="menu(item.title)">{{
                  item.title
                }}</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list>
          <v-list nav dense v-if="barra == true && estado == 'Admin'">
            <v-list-item
              v-for="item in menuItemsAdmin"
              :key="item.title"
              active-class="deep-purple--text text--accent-4"
            >
              <v-list-item-content>
                <v-list-item-title @click="menu(item.title)">{{
                  item.title
                }}</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list>
          <v-list nav dense v-if="barra == true && estado == 'Client'">
            <div style="background: red">a</div>
            <v-list-item
              v-for="item in menuItemsClient"
              :key="item.title"
              @click="menu(item.title)"
              active-class="deep-purple--text text--accent-4"
            >
              <v-list-item-content>
                <v-list-item-title>{{ item.title }}</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list>
        </v-navigation-drawer>
        <v-app-bar app>
          <v-app-bar-nav-icon
            @click.stop="drawer = !drawer"
          ></v-app-bar-nav-icon>
          <v-spacer></v-spacer>
          <br />
          <p>COVID HELPER</p>
          <v-spacer></v-spacer>
        </v-app-bar>
            <v-dialog v-model="dialog" max-width="400">
      <v-card color="#F2f2f2" light>
        <v-spacer></v-spacer>
        <v-card-title class="headline">COVID | HELPER</v-card-title>
        <v-spacer></v-spacer>
        <form
          ref="form"
          @submit.prevent="senduser"
          :v-model="valid"
          lazy-validation
          v-show="this.loginDialog == 0"
        >
          <v-card-text>
            <v-text-field
              v-model="login.user"
              :rules="[
                () =>
                  !!login.user ||
                  'La cuenta de usuario es necesario para iniciar sessin',
              ]"
              label="Cuenta de usuario"
              required
              prepend-icon="person"
            ></v-text-field>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="#F26522" type="submit">Iniciar sessin</v-btn>
            <v-spacer></v-spacer>
          </v-card-actions>
        </form>
        <form
          ref="form"
          @submit.prevent="sendusercomplet"
          :v-model="valid"
          lazy-validation
          v-show="this.loginDialog == '1'"
        >
          <v-card-text>
            <v-text-field
              v-model="login.password"
              :rules="[
                () =>
                  !!login.password ||
                  'La contrasea es requerido para iniciar sessin',
              ]"
              label="Contrasea"
              required
              prepend-icon="lock"
              :append-icon="show1 ? 'visibility' : 'visibility_off'"
              :type="show1 ? 'text' : 'password'"
              data-vv-name="pass"
              name="input-10-1"
              @click:append="show1 = !show1"
            ></v-text-field>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="#F26522" type="submit">Iniciar sessin</v-btn>
            <v-spacer></v-spacer>
          </v-card-actions>
        </form>
      </v-card>
    </v-dialog>
        <v-dialog v-model="dialogMessage" max-width="290">
      <v-card>
        <v-card-title class="headline">UPS!</v-card-title>
        <v-card-text>{{ message }}</v-card-text>
      </v-card>
    </v-dialog>
</div>
</template>
<script>
import axios from "axios";
export default {
  name: "App",
  data: () => ({
    menuprueba: {
      Dashboard: [["Dashboard ", "web_asset", "trending_up"]],
      "Gestin de Covid Fast Helper": [
        ["Registro de Informacin", "flight", "add"],
        ["Registro de Formularios", "", "post_add"],
        ["Preguntas de Formularios", "", "?"],
        ["Registro de Preguntas Frecuentes", "", "?"],
      ],
      Reportes: [
        ["Reportes de Informacin", "description", ""],
        ["Reportes de Formularios", "", ""],
        ["Reportes de Preguntas Frecuentes", "", ""],
      ],
      "Gestin de Usuarios": [
        ["Registro de Usuarios", "group", "person_add"],
        ["Roles y Permisos", "", "star_outline"],
      ],
    },
    items: [{ title: "Mi Perfil" }, { title: "Cerrar Session" }],
    selection: [],
    barra: false,
    drawerlogin: null,
    menucal: false,
    dialogRegister: false,
    cont: 0,
    loginDialog: 0,
    abbr: "",
    estado: "home",
    ocultar: 0,
    dynamic: "",
    dialogMessage: false,
    message: "",
    show1: "",
    pass: "",
    valid: false,
    dialog: false,
    drawerlog: false,
    drawer: false,
    login: {
      user: "",
      password: "",
      usr_token: "",
    },
    keyMenus: true,
    menuItems: [
      { icon: "trending_up", title: "Preguntas frecuentes" },
      { icon: "trending_up", title: "Nuestras Areas" },
      { icon: "trending_up", title: "Informacin" },
      { icon: "trending_up", title: "Servicios" },
      { icon: "trending_up", title: "contactanos" },
      { icon: "trending_up", title: "empezar" },
    ],
    menuItemsAdmin: [
      {
        icon: "trending_up",
        title: "Tablero Administrativo",
      },
      {
        icon: "trending_up",
        title: "Usuarios",
      },
      {
        icon: "trending_up",
        title: "Formularios",
      },
      {
        icon: "subscriptions",
        title: "Administracin de Informaciones",
      },
      {
        icon: "subscriptions",
        title: "Reportes",
      },
      {
        icon: "subscriptions",
        title: "Preguntas de Formularios",
      },
      {
        icon: "history",
        title: "Preguntas Frecuentes",
      },
      {
        icon: "watch_later",
        title: "Reportes Realizados",
      },
    ],
    menuItemsClient: [
      {
        icon: "trending_up",
        title: "Tablero",
      },
      {
        icon: "subscriptions",
        title: "Encuestas",
      },
      {
        icon: "subscriptions",
        title: "Informaciones",
      },
    ],
    listCareer: [],
    progress: false,
    contForm: 0,
    studentUnifranz: {
      code: "",
      name: "",
      lastname: "",
      email: "",
      celular: "",
      sede: "",
      genero: "",
      date: "",
      password: "",
    },
    student: {
      code: "",
      name: "",
      lastName: "",
      sede: "",
      genero: "",
      correo: "",
      ci: "",
      date: "",
      phone: "",
    },
    careers: [],
    headquarters: [],
    listGenero: [
      { abbr: "F", title: "FEMENINO" },
      { abbr: "M", title: "MASCULINO" },
    ],
  }),
  computed: {
    computedItems() {
      return this.items.map(this.mapItem);
    },
  },
  created: function() {
    window.addEventListener("scroll", this.handleScroll);
  },
  destroyed: function() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  watch: {
    menu(val) {
      val && setTimeout(() => (this.$refs.picker.activePicker = "YEAR"));
    },
  },
  mounted() {
    if (localStorage.getItem("user") == "admin") {
      this.barra = true;
      this.estado = "Admin";
    }
    if (localStorage.getItem("user") == "client") {
      this.barra = true;
      this.estado = "Client";
    }
    this.dynamic = "black";
  },
  methods: {
    botonMenu(data) {
      console.log(data);
      if (data == "1") {
        this.cerrarSession();
      }
    },
    cerrarSession() {
      localStorage.clear();
      this.$router.push("/");
      this.barra = false;
      this.estado = "home";
    },
    handleScroll: function(event) {
      if (event.target.scrollingElement.scrollTop == "0") {
        this.dynamic = "black";
      } else {
        this.dynamic = "#f2f2f2";
      }
    },
    isMobile() {
      if (
        /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
          navigator.userAgent
        )
      ) {
        return true;
      } else {
        return false;
      }
    },
    menu(title) {
      if (title == "empezar") {
        this.dialog = true;
        this.keyMenus = false;
      }
      //client
      if (title == "Tablero") {
        return this.$router.push("Tablero");
      }
      if (title == "Encuestas") {
        return this.$router.push("surveys");
      }
      if (title == "Informaciones") {
        return this.$router.push("information");
      }
      //admin
      if (title == "Tablero Administrativo") {
        return this.$router.push("dashboard");
      }
      if (title == "Usuarios") {
        return this.$router.push("users");
      }
      if (title == "Registro de Formularios") {
        return this.$router.push("createForms");
      }
      if (title == "Administracin de Informaciones") {
        return this.$router.push("createInformation");
      }
      if (title == "Reportes") {
        return this.$router.push("report");
      }
      if (title == "Preguntas de Formularios") {
        return this.$router.push("questions");
      }
      if (title == "Preguntas Frecuentes") {
        return this.$router.push("createQuestionsFrequents");
      }
      if (title == "Reportes Realizados") {
        return this.$router.push("createReports");
      }
    },
    senduser() {
      this.dialogMessage = false;
      axios
        .post("https://covidhelper-backend.herokuapp.com/login/customer", {
          cst_user_name: this.login.user,
        })
        .then((result) => {
          if (result.data.message == "Usuario no existe") {
            this.senduserAdmin();
          }
          if (result.data.message == "Usuario existe") {
            this.loginDialog = 1;
          }
        });
    },
    senduserAdmin() {
      axios
        .post("https://covidhelper-backend.herokuapp.com/login/user", {
          usr_name: this.login.user,
        })
        .then((result) => {
          if (result.data.message == "Usuario no existe") {
            this.dialogMessage = true;
            this.message = "Escribiste mal el nombre de usuario";
          }
          if (result.data.message == "Usuario existe") {
            this.loginDialog = 1;
          }
        });
    },
    sendusercomplet() {
      axios
        .post("https://covidhelper-backend.herokuapp.com/login/customers", {
          cst_user_name: this.login.user,
          cst_password: this.login.password,
        })
        .then((result) => {
          if (
            result.data.message == "Contrasea incorrecta" ||
            result.data.message == "Usuario no existe"
          ) {
            this.sendusercompletAdmin();
          } else {
            localStorage.setItem("user_token", result.data.token);
            localStorage.setItem("user", "client");
            this.$router.push("Tablero");
            this.dialogMessage = false;
            this.dialog = false;
            this.estado = "Client";
            this.barra = true;
          }
        });
    },
    sendusercompletAdmin() {
      axios
        .post("https://covidhelper-backend.herokuapp.com/login/users", {
          usr_name: this.login.user,
          password: this.login.password,
        })
        .then((result) => {
          if (
            result.data.message == "Contrasea incorrecta" ||
            result.data.message == "Usuario no existe"
          ) {
            this.dialogMessage = true;
            this.message = "Contrasea Incorrecta";
          } else {
            localStorage.setItem("user_token", result.data.token);
            localStorage.setItem("user", "admin");

            axios
              .get(
                "https://covidhelper-backend.herokuapp.com/specialism/" +
                  result.data.payload.id
              )
              .then((res) => {
                if (res.data != "no tiene especialidades") {
                  localStorage.setItem("id_area", res.data.id);
                }
                this.$router.push("dashboard");
                this.dialogMessage = false;
                this.dialog = false;
                this.estado = "Admin";
                this.barra = true;
              });
          }
        });
    },
  },
};
</script>
<style>
/* .todo{
 font-family: "Uni Neue Light";
  src:h url("assets/fonts/UniNeueLight.otf") format("otf");
} */
.titlewhite {
  color: #f2f2f2;
}
</style>
