import Vue from "vue";

import Router from "vue-router";

import Home from "./views/Home.vue";
import createForms from "./views/admin_/createForms.vue";
import createInformation from "./views/admin_/createInformation.vue";
import createQuestionsFrequents from "./views/admin_/createQuestionsFrequents.vue";
import createReports from "./views/admin_/createReports.vue";
import dashboard from "./views/admin_/dashboard.vue";
import questions from "./views/admin_/questions.vue";
import report from "./views/admin_/report.vue";
import users from "./views/admin_/users.vue";
import dashboardClient from "./views/client_/dashboard.vue";
import information from "./views/client_/information.vue";
import surveys from "./views/client_/surveys.vue";
import viewForms from "./views/client_/viewForms.vue";
import allClient from "./views/client_/components_/allClient.vue"

Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      name: "home",
      component: Home,
    },
    {
      path: "/Carrera/:id",
      name: "Carrera",
      component: () =>
        import(/* webpackChunkName: "publicaciones" */ "./views/Career.vue"),
      props: true,
      beforeRouteUpdate(to, from, next) {
        this.id = to.params.id;
        next();
      },
    },
    /*{
            path:'/footer',
            name: 'footer',
            component: lista
        },*/
    {
      path: "/dashboard",
      name: "dashboard",
      component: dashboard,
    },
    {
      path: "/Tablero",
      name: "Tablero",
      component: dashboardClient,
    },
    {
      path: "/createForms",
      name: "createForms",
      component: createForms,
    },
    {
      path: "/viewForms",
      name: "viewForms",
      component: viewForms,
    },
    {
      path: "/createInformation",
      name: "createInformation",
      component: createInformation,
    },
    {
      path: "/createQuestionsFrequents",
      name: "createQuestionsFrequents",
      component: createQuestionsFrequents,
    },
    {
      path: "/createReports",
      name: "createReports",
      component: createReports,
    },
    {
      path: "/questions",
      name: "questions",
      component: questions,
    },
    {
      path: "/report",
      name: "report",
      component: report,
    },
    {
      path: "/dashboard",
      name: "dashboard",
      component: dashboard,
    },
    {
      path: "/users",
      name: "users",
      component: users,
    },
    {
      path: "/information",
      name: "information",
      component: information,
    },
    {
      path: "/surveys",
      name: "surveys",
      component: surveys,
    },
    {
      path: "/allClient",
      name: "allClient",
      component: allClient,
    },
  ],
});
