<template>
  <v-data-table
    :headers="headers"
    :items="listInformations"
    :search="search"
    sort-by="calories"
    class="elevation-1"
  >
    <template v-slot:top>
      <v-toolbar flat color="white">
        <v-toolbar-title>LISTA DE INFORMACINES</v-toolbar-title>
        <v-divider
          class="mx-4"
          inset
          vertical
        ></v-divider>
        <v-text-field
        v-model="search"
        append-icon="mdi-magnify"
        label="Buscar"
        single-line
        hide-details
      ></v-text-field>
        <v-spacer></v-spacer>
        <v-dialog v-model="dialog" max-width="1000px">
          <template v-slot:activator="{ on }">
            <v-btn color="primary" dark class="mb-2" v-on="on" @click="prueba">CREAR NUEVA INFORMACIN</v-btn>
          </template>
          <v-card>
             <form ref="form" @submit.prevent="saveNewInformation" :v-model="valid" lazy-validation>
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12" sm="6" md="6">
                    <v-text-field  label="Nombre" required  v-model='Information.inf_name'></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="6">
                    <v-text-field  label="Descripcin" required  v-model='Information.description'></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="6">
                    <v-file-input multiple label="File input" v-model='Information.file'></v-file-input>
                  </v-col>
                  <v-col cols="12" sm="6" md="6">
                    <v-select  :items="listTypesInfo" label="Tipo de Informacin" required item-value="id" item-text="inf_type_name" v-model='Information.id_typ_info'></v-select>
                  </v-col>
                  <v-col cols="12" sm="6" md="6">
                    <v-select  :items="listSpecialism" label="Tipo de Informacin" required item-value="id" item-text="spe_name" v-model='Information.id_specialisms'></v-select>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialog=false">CANCELAR</v-btn>
              <v-btn color="blue darken-1" text type="submit">GUARDAR</v-btn>
            </v-card-actions>
            </form> 
          </v-card>
        </v-dialog>


          <!-- <v-dialog v-model="dialogEdit" max-width="1000px">
          <v-card>
            <v-card-title>
              <span class="headline">EDICIN DE USUARIO</span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>

                  <v-col cols="12" sm="6" md="6">
                    <v-text-field  label="Nombres" required :value=Information.inf_name v-model='Information.inf_name'></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="6">
                    <v-text-field  label="Apellido Paterno" required :value=Information.id_typ_info v-model='Information.id_typ_info'></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="6">
                    <v-text-field label="Apellido Materno" required :value=Information.usr_surname2 v-model='Information.usr_surname2'></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="6">
                    <v-text-field label="Correo" required :value=Information.email v-model='Information.email'></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="6">
                    <v-select  :items="listTypesInfo" label="Tipo de informacin" required item-value="id" item-text="inf_type_name" v-model='user.id_rol'></v-select>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialogEdit=false">CANCELAR</v-btn>
              <v-btn color="blue darken-1" text @click="editUser()">GUARDAR</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog> -->
      </v-toolbar>
    </template>
    <template v-slot:item.action="{ item }">
      <v-icon
        small
        class="mr-2"
        @click="editItem(item)"
      >
        edit
      </v-icon>
      <v-icon
        small
        @click="deleteItem(item)"
      >
        delete
      </v-icon>
    </template>
    <template v-slot:no-data>
      <v-btn color="primary" @click="initialize">Reset</v-btn>
    </template>
  </v-data-table>
</template>
<script>
import axios from 'axios';
import jwt_decode from 'jwt-decode';  
  export default {
    data: () => ({
      imageName:'',
      imageFile: '',
        imageUrl: '',
         search: '',
        model: null,
      dialogEdit:false,
      dialog: false,
      Estado:"",
      estado: [
           {id:'0',name:'Inactivo'},
            {id:'1',name:'Activo'}
           ],
      Rol:"",
      rol: [
           {id:'0',name:'Administrador General'},
            {id:'1',name:'Administrador Promedio'}
           ],
      headers: [
        {
          text: 'Nro',
          align: 'left',
          sortable: false,
          value: 'id',
        },
        { text: 'Nombre', value: 'inf_name' },
        { text: 'Descripcin', value: 'description' },
        { text: 'Enlace', value: 'inf_link' },
        { text: 'Especialidad', value: 'covid_specialism.spe_name' },
        { text: 'Tipo de informacin', value: 'covid_types_info.inf_type_name' },
        { text: 'Creado por', value: 'covid_user.usr_name' },
        { text: 'Estado', value: 'inf_state' },
        { text: 'Acciones', value: 'action', sortable: false },
      ],
      desserts: [],
      listInformations:[],
      listTypesInfo:[],
      listSpecialism:[],
      editedIndex: -1,
      editedItem: {
        name: '',
        calories: 0,
        fat: 0,
        carbs: 0,
        protein: 0,
      },
      userID:'',
      Information:{
        file:'',
        id_specialisms:'',
        id_typ_info:'',
        id_user:'',
        inf_name:'',
        inf_link:'',
        inf_state:'',
        description:''
      },
      defaultItem: {
        name: '',
        calories: 0,
        fat: 0,
        carbs: 0,
        protein: 0,
      },
    }),

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'Nuevo usuario' : 'Edit Item'
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
    },

    created () {
      this.initialize()
    },
    mounted(){
    axios.get('https://covidhelper-backend.herokuapp.com/information')
    .then((result) => {
        console.log(result.data)
        this.listInformations=result.data
    }).catch((err) => {
        console.log(err)
    });
    axios.get('https://covidhelper-backend.herokuapp.com/infoTypesOn')
    .then((result) => {
        console.log(result.data)
        this.listTypesInfo=result.data
    }).catch((err) => {
        console.log(err)
    });
    axios.get('https://covidhelper-backend.herokuapp.com/specialism')
    .then((result) => {
        console.log(result.data)
        this.listSpecialism=result.data
    }).catch((err) => {
        console.log(err)
    });
    },

    methods: {
            pickFile () {
        
        this.$refs.image.click ()
        console.log(this.imageName)
        },
        onFilePicked (e) {

          const files = e.target.files
          // console.log(e)
          if(files[0] !== undefined) {
            this.imageName = files[0].name
            if(this.imageName.lastIndexOf('.') <= 0) {
              return
            }
            const fr = new FileReader ()
            fr.readAsDataURL(files[0])
            fr.addEventListener('load', () => {
              this.imageUrl = fr.result
              // console.log('hehe')
              console.log(files[0].name)
              // console.log('hehe')
              // this.newCourse.imageFile = files[0]
              axios.post('https://passunifranz-backapp.herokuapp.com/image',{"image":files[0]},{headers: {'Content-Type': 'multipart/form-data'}})
              .then((result) => {
               console.log(result) 
              }).catch((err) => {
                console.log(err)
              });
            })
          } else {
            this.imageName = ''
            this.imageFile = ''
            this.imageUrl = ''
          }
          },
        prueba(){
            console.log('prueba')
            console.log(this.search)
        },
      xd () {
        axios.get('https://passunifranz-backweb.herokuapp.com/users')
        .then((result) => {
            console.log(result.data)
            this.listUsers=result.data
        }).catch((err) => {
            console.log(err)
        });
        
      },

      editItem (item) {
        this.userID=item.id
        this.user.id_rol=item.id_rol
        this.user.usr_name=item.usr_name
        this.user.usr_surname1=item.usr_surname1
        this.user.usr_surname2=item.usr_surname2
        this.user.email=item.email
        this.user.password=item.password
        this.user.usr_state=item.usr_state
        this.user.id_responsable=item.id_responsable
        this.dialogEdit = true
      },

      deleteItem (item) {
        console.log(item.id)
        axios.put('https://covidhelper-backend.herokuapp.com/user/'+item.id,
        {
          "id_rol":item.id_rol,
          "usr_name":item.usr_name,
          "usr_surname1":item.usr_surname1,
          "usr_surname2":item.usr_surname2,
          "email":item.email,
          "password":item.password,
          "usr_state": 0
        }
        )
        .then((result) => {
          console.log(result.data)
        })
      },
      editUser(){
        console.log(this.user)
        axios.put('https://covidhelper-backend.herokuapp.com/user/'+this.userID,
        {
          "id_rol":this.user.id_rol,
          "usr_name":this.user.usr_name,
          "usr_surname1":this.user.usr_surname1,
          "usr_surname2":this.user.usr_surname2,
          "email":this.user.email,
          "password":this.user.password,
          "usr_state": this.user.usr_state
        }
        )
        .then((result) => {
          console.log(result.data)
          this.dialogEdit = false
        })
      },
      saveNewInformation(){
        console.log(this.Information)
        var formData = new FormData();
        formData.append("informacion",this.Information.file[0]);
        axios.post('https://covidhelper-backend.herokuapp.com/guardar/informacion', formData, {
            headers: {
              'Content-Type': 'multipart/form-data'
            }
        })
        .then((result) => {
          this.sendDataInfo(this.Information,result.data.urlimage)
        })
      },
      sendDataInfo(info,url){
      const token=jwt_decode(localStorage.getItem('user_token')); 
      axios.post('https://covidhelper-backend.herokuapp.com/register/information',
      {
        "id_specialisms":info.id_specialisms,
        "id_typ_info":info.id_typ_info,
        "id_user":token.id,
        "inf_name":info.inf_name,
        "inf_link":url,
        "inf_state":"1",
        "description":info.description
      })
      .then((result) => {
        console.log(result)
      })
      },
      close () {
        this.dialog = false
        setTimeout(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        }, 300)
      },

      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.desserts[this.editedIndex], this.editedItem)
        } else {
          this.desserts.push(this.editedItem)
        }
        this.close()
      },
    },
  }
</script>